.header {
  padding: 10px;

  display: flex;
  align-items: center;

  grid-area: h;

  background: linear-gradient(90deg, rgba(25, 58, 47, 0.7) 0%, rgba(255, 255, 255, 0.2) 100%);
  /* background: linear-gradient(90deg, rgba(112,79,143,0.8) 0%, rgba(169,176,0,0.5) 100%); */
}

.header img {
  margin-right: 10px;
  height: 100%;
  
  pointer-events: none;
  animation: rotateLogo infinite 20s linear;
}

@keyframes rotateLogo {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.header span {
  font-weight: 500;
  font-size: 18px;
  color: rgb(84, 232, 251);
}
